body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.panel {
 
  padding-bottom:120px;
  padding-top:30px;
}
.panel.col-md-3 { 
  background:#f4f4f4;

}
.panel button.btn-question{
  font-size: 1.1rem;
  font-weight: 500;
  text-align: left;
}
.panel button svg{
  fill:white;
  width: 30px;
  float:left;
  margin-right: 30px;
}
.survey--question {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin: 15px 0px;
  padding-left: 130px;
  padding-right: 100px;
  min-height: 160px;
  background: #eee;
}
.survey--question ul {
  margin-top: 10px;
}
.survey--question li {
    border: 1px solid rgba(0,0,0,0.1);
    list-style: none;
    background: white;
}
.question-buttons {
  position: absolute;
  left: 0px;
  width: 130px;
  top: 0px;
  bottom: 0px;
  opacity: 0;
  transition: all 1s;
}


.survey--question:hover .question-buttons {
  opacity: 1;
}
.question-buttons-right {
  position: absolute;
  right: 0px;
  width: 94px;
  top: 0px;
  bottom: 0px;
  opacity: 0;
  transition: all 1s;
}

.survey--question:hover .question-buttons-right {
  opacity: 1;
}

.survey--question  button.update-question.btn.btn-info {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.survey--question button.delete-question {
  position: absolute;
  right: 0px;
  top: 0px;
  bottom: 0px;
  width: 50%;
  border: 5px solid firebrick;
}

.survey--question button.order-bottom {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 50%;
  height: 50%;
  font-size: 40px;
  color: white;
}

button.order-top {}

.survey--question button.order-top {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 50%;
  height: 50%;
  font-size: 50px;
  color: whte;
}